<template>
  <div class="people">
    <div class="topButton">
      <el-button v-show="addsy" size="mini" @click="showAddDM()" type="primary"></el-button>

      <el-button v-show="editsy" size="mini" type="warning" @click="showChange()"></el-button>

      <!-- <el-button size="mini" type="info" @click="mShow()"></el-button> -->

      <el-button v-show="deletesy" size="mini" type="danger" @click="showDelete()"></el-button>
    </div>
    <!--  -->
    <div class="list">
      <div class="search" v-show="searchsy">
        <el-input v-model="search" placeholder=""></el-input>
		 <el-button size="mini" type="number" @click="selctStudent()"></el-button>
      </div> 

      <el-table
        :data="user.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%"
        highlight-current-row
        border
        @current-change="handleCurrentsChange"
      >
        <el-table-column label="" prop="d_no"></el-table-column>

        <el-table-column label="" prop="s_no"></el-table-column>

        <el-table-column label="" prop="s_name"></el-table-column>

        <el-table-column label="" prop="s_class"></el-table-column>

        <el-table-column label="" prop="t_name"></el-table-column>

        <el-table-column label="" prop="is_dorm_header"></el-table-column>

      <!--  <el-table-column label="">
            <button @click="selectAttnClass()"></button>
            <button @click="selectUseElec()"></button>
            <button @click="selectNotReturn()"></button>
        </el-table-column> -->
<el-table-column label="" align="center" min-width="100">
      <template slot-scope="scope">
        <button  @click="selectAttnClass(scope.row.s_no)"></button>
        <button  @click="selectUseElec(scope.row.s_no)"></button>
		<button  @click="selectNotReturn(scope.row.s_no)"></button>
      </template>
    </el-table-column>
        <!-- <el-table-column label="" prop="floor"></el-table-column>

        <el-table-column label="" prop="floor"></el-table-column>

        <el-table-column label="" prop="floor"></el-table-column>

        <el-table-column label="" prop="floor"></el-table-column> -->

      </el-table>

      <!--  -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="user.length"
        ></el-pagination>
      </div>
    </div>

    <!--  -->

    <div v-if="flag" class="addD">
      <div class="title">
        <h2></h2>
        <i class="el-icon-close" @click="clossAdd()"></i>
      </div>
      <div class="add">
        <el-form ref="form" :model="addDormitoryM" label-width="80px" :rules="rules">
          <el-form-item label="">
            <el-input v-model="addDormitoryM.d_no"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addDormitoryM.s_no" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addDormitoryM.s_name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addDormitoryM.s_class"></el-input>
          </el-form-item>
          <el-form-item label="">
            <!-- <el-input v-model="addDormitoryM.t_name"></el-input> -->
			<el-select v-model="addDormitoryM.t_name" placeholder="">
			  <el-option value="" disabled selected></el-option>
			  <el-option v-for="item in list" :key="0" :value="item.u_name" :label="item.u_name"></el-option>
			</el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="addDormitoryM.is_dorm_header" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item label="">
            <el-input v-model="addDormitoryM.floor"></el-input>
          </el-form-item> -->
          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="addDormitoryMr()"></el-button>
              <el-button @click="addRest()"></el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>
    <!--  -->

    <div v-if="flag2" class="changeD">
      <div class="title">
        <h2></h2>
        <i class="el-icon-close" @click="clossChange()"></i>
      </div>

      <div class="change">
        <el-form ref="form" :model="changeList" label-width="80px">
          <el-form-item label="">
            <el-input v-model="changeList.s_name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="changeList.s_no"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="changeList.s_class"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="changeList.t_name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="changeList.is_dorm_header" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item label="">
            <el-input v-model="changeList.floor"></el-input>
          </el-form-item> -->
          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="changeDormitoryM()"></el-button>
              <el-button @click="changeRest()"></el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!--  -->
    <div class="delete" v-if="flag3">
      <div class="title">
        <span></span>
        <i class="el-icon-close" @click="closeDelete()"></i>
      </div>
      <div class="main">
        <i class="el-icon-warning"></i>
        <span></span>
      </div>
      <div class="deleteButton">
        <el-button size="mini" @click="closeDelete()"></el-button>
        <el-button size="mini" type="danger" @click="deleteInfo()"></el-button>
      </div>
    </div>

    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag2"></div>
    <div class="popContainer" v-if="flag3"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: [],
      flag: false,
      flag2: false,
      flag3: false,
      flag4:false,
	  addsy:false,
	  editsy:false,
	  deletesy:false,
	  searchsy:false,
      search: "",
      currentPage: 1, //
      pagesize: 5,
      addDormitoryM: [
        {
          d_no:'',
          s_no:'',
          s_name: "",
          s_class:"",
          t_name:"",
          is_dorm_header: ""
         
        }
      ],
      changeList: [
        {
          d_no:'',
          s_no:'',
          s_name: "",
          s_class:"",
          t_name:"",
          is_dorm_header: ""
          
        }
      ],
      deleteList: [
        {
          d_no:'',
          s_no:'',
          s_name: "",
          s_class:"",
          t_name:"",
          is_dorm_header: ""
         
        }
      ],
      rules: {
        d_no: [
          { required: true, message: "", trigger: "blur" },
          { max: 3, message: "3", trigger: "blur" }
        ],
       
        s_no: [
          { required: true, message: "", trigger: "blur" },
          { max: 11, message: "11", trigger: "blur" }
        ]
      },
	  list:[]
    };
  },
  created() {
    let _this = this;
	let u_type = this.$store.getters.userNType;
	let u_no = this.$store.getters.userNo;
	let u_name = this.$store.getters.userName;
	let src = "";
	 /* else if(u_type == ""){
		src = "?ssh=" +encodeURIComponent(u_ssh);
	} */ 
	
	console.log(u_type);
	let dNo = this.$store.getters.dNo;
    this.$axios
      .get("/dormitorymbr?b_name=" + u_name + "&ssh=" + encodeURIComponent(dNo) + "&u_type=" + u_type)
      .then(function(res) {
        if (res.data) {
          _this.user = res.data;
          // console.log(res.data)
        }
      })
      .catch(function(err) {
        if (err.response) {
          console.log(err.response);
        }
      });
	  
	  		 
	  		  this.$axios
	  		    .get("/getUser?u_type=")
	  		    .then(res => {
	  		      if (res.data.code === 200) {
					  console.log(res);
	  				  _this.list = res.data.data;
					  console.log(_this.list);
	  		      }
	  		    })
	  		    .catch(function(err) {
	  		      if (err.response) {
	  		        console.log(err.response);
	  		        this.$message("");
	  		      }
	  		    });
				
	if(u_type == ""){
		this.addsy = true;
		this.editsy=true;
		this.deletesy=true;
		this.searchsy=true;
	}
  },
  inject: ["reload"],
  methods: {
    selectAttnClass(val){
      this.$router.push({ path: "/home/dormMbrCs?val=" + encodeURIComponent(val) });
      console.log(_this.changeList.s_no)
    },
    selectUseElec(val){
      this.$router.push({ path: "/home/dormMbrElec?val=" + encodeURIComponent(val) });
    },
    selectNotReturn(val){
      this.$router.push({ path: "/home/dormMbrNR?val=" + encodeURIComponent(val) });
    },
    showAddDM() {
      this.flag = !this.flag;
    },
    addDormitoryMr() {
      if(this.addDormitoryM.s_name){
         let _this = this;
      this.$axios
        .post("/dormitorymbr", {
          d_no:_this.addDormitoryM.d_no,
          s_no: _this.addDormitoryM.s_no,
          s_name: _this.addDormitoryM.s_name,
          s_class: _this.addDormitoryM.s_class,
          t_name: _this.addDormitoryM.t_name,
          is_dorm_header: _this.addDormitoryM.is_dorm_header
          // floor: _this.addDormitory.floor
        })
        .then(res => {
          if (res.data.code === 200) {
            this.$message("");
            this.flag = !this.flag;
            // this.flag2=!this.flag2
            // this.$router.go(0)
            this.reload();
            console.log(_this.addDormitoryM.d_no)
          }
        })
        .catch(function(err) {
          if (err.response) {
            console.log(err.response);
            this.$message("");
          }
        });
      }else{
        this.$message('')
      }
    },
    changeDormitoryM() {
      let _this = this;
      this.$axios
        .put("/dormitorymbr", {
          d_no: _this.changeList.d_no,
          s_name: _this.changeList.s_name,
          s_no: _this.changeList.s_no,
          s_class: _this.changeList.s_class,
          t_name: _this.changeList.t_name,
          is_dorm_header: _this.changeList.is_dorm_header
          // floor: _this.changeList.floor
        })
        .then(res => {
          if (res.data.code === 200) {
            this.$message("");
            this.flag2 = !this.flag2;
            // this.flag2=!this.flag2
            // this.$router.go(0)
            this.reload();
          }
        })
        .catch(function(err) {
          if (err.response) {
            console.log(err.response);
            this.$message("");
          }
        });
    },
    deleteInfo() {

      let _this = this;
      console.log(_this.deleteList.d_no);
      this.$axios
        .delete("/dormitorymbr", {
          data: {
            d_no: _this.deleteList.d_no
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            this.$message("");
            this.flag3 = !this.flag3;
            this.reload();
          }
        })
        .catch(err => {
          if (err.response) {
            console.log(err.response);
            this.$message("");
          }
        });
    },

    showDelete() {
      if (this.deleteList.d_no) {
        this.flag3 = !this.flag3;
      } else {
        this.$message("");
      }
    },

    clossAdd() {
      this.flag = !this.flag;
      this.$message("");
    },
    addRest() {
      this.addDormitoryM = [];
    },
    // 
    setCurrent(row) {
      this.$refs.singleTable.setCurrentRow(row);
    },
    handleCurrentsChange(val) {
      this.currentRow = val;
      this.changeList = val;
      this.deleteList = val;

      // this.changeList = []
    },

    handleSizeChange: function(size) {
      this.pagesize = size;
      console.log(this.pagesize); //
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
      console.log(this.currentPage); //
    },

    clossChange() {
      this.flag2 = !this.flag2;
      this.$message("");
    },
    showChange() {
      if (this.changeList.d_no) {
        this.flag2 = !this.flag2;
      } else {
        this.$message("");
      }
    },
    closeDelete() {
      this.flag3 = !this.flag3;
      this.$message("");
    },
    changeRest() {
      this.changeList = [];
    },
    mShow(){
       this.flag4 = !this.flag4;
    },selctStudent(){
		let _this = this;
		console.log(_this.search);
		this.$axios
		  .get("/dormitorymbr?s_no=" +encodeURIComponent( _this.search))
		  .then(function(res) {
		    if (res.data) {
		      _this.user = res.data;
		      // console.log(res.data)
		    }
		  })
		  .catch(function(err) {
		    if (err.response) {
		      console.log(err.response);
		    }
		  });
     /* let _this = this;
      this.$axios
        .get("/dormitorymbr", {
          data: {
            s_no: _this.search
          }
        })
        .then(res => {
          // if (res.data.code === 200) {
          //   this.$message("");
          //   this.flag3 = !this.flag3;
          //   this.reload();
          // }
          // this.reload();
        })
        .catch(err => {
          if (err.response) {
            console.log(err.response);
            this.$message("");
          }
        }); */
    }
  }
};
</script>

<style>
.topButton {
  width: 100%;
  height: 50px;
  line-height: 50px;
  padding-bottom: 20px;
}
.el-table-column {
  text-align: center;
}
.el-submenu .el-menu-item:hover {
  background-color: rgb(232, 243, 255);
}
.list {
  position: relative;
}
.addD {
  position: absolute;
  z-index: 1001;
  width: 400px;
  height: 550px;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
}
.addD .title {
  height: 50px;
  width: 100%;
  text-align: center;
  line-height: 30px;
  /* border-bottom: 1px solid #dcdfe6; */
  color: #409eff;
  text-shadow: 2px 2px 2px #ccc;
}
.addD .title i {
  position: relative;
  top: -55px;
  right: -170px;
  color: black;
  font-size: 20px;
}
.addD .add {
  margin-right: 40px;
  margin-top: 30px;
}
.addD .addButton {
  margin-left: 10px;
  margin-top: 30px;
}
.addD .addButton .el-button {
  width: 110px;
}
.addD .el-select {
  width: 280px;
}
.changeD {
  position: absolute;
  z-index: 1001;
  width: 400px;
  height: 500px;
  /* border:1px solid #dcdfe6; */
  left: 0;
  top: -50px;
  bottom: 0;
  right: 0;
  margin: auto;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
}
.changeD .title {
  height: 50px;
  width: 100%;
  text-align: center;
  line-height: 30px;
  /* border-bottom: 1px solid #dcdfe6; */
  color: #409eff;
  text-shadow: 2px 2px 2px #ccc;
}
.changeD .title i {
  position: relative;
  top: -55px;
  right: -170px;
  color: black;
  font-size: 20px;
}
.changeD .change {
  margin-right: 40px;
  margin-top: 30px;
}
.changeD .addButton .el-button {
  width: 115px;
}
.changeD .el-select {
  width: 280px;
}
.delete {
  position: absolute;
  z-index: 1001;
  width: 430px;
  height: 140px;
  border: 1px solid #dcdfe6;
  left: 0;
  top: -50px;
  bottom: 0;
  right: 0;
  margin: auto;
  background-color: #fff;
  border-radius: 10px;
}
.title {
  width: 100%;
  height: 45px;
  line-height: 45px;
}
.title span {
  float: left;
  margin-left: 20px;
}
.delete .title i {
  float: right;
  margin-top: 10px;
  margin-right: 10px;
}
.delete .main {
  height: 50px;
  width: 100%;
  line-height: 50px;
  margin-left: 50px;
}
.delete .main i {
  color: #e6a23c;
  font-size: 20px;
  margin-right: 10px;
}
.delete .main span {
  margin-bottom: 5px;
}
.delete .deleteButton {
  float: right;
  margin-right: 20px;
  margin-top: 7px;
}

.popContainer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 500;
}
.page {
  position: absolute;
  right: 0;
  bottom: -50px;
}
.search {
  position: relative;
  top: -12px;
}
</style>


